export default {
  name: "author",
  data: function data() {
    return {
      dialogChoseStatus: true,
      form: {
        sex: "",
        age: "",
        job: "",
        marriage: ""
      },
      rules: {
        sex: [{
          required: true,
          message: "请选择您的年龄",
          trigger: "blur"
        }],
        age: [{
          required: true,
          message: "请选择您的年龄",
          trigger: "change"
        }],
        job: [{
          required: true,
          message: "请选择工作性质",
          trigger: "change"
        }],
        marriage: [{
          required: true,
          message: "请选择婚姻状况",
          trigger: "change"
        }]
      },
      textShow: 0
    };
  },
  mounted: function mounted() {},
  methods: {
    handleClose: function handleClose(formName) {
      var _this = this;

      this.$refs[formName].validate(function (valid) {
        if (valid) {
          console.log(_this.form);
          var status = "您是一位" + _this.form.age + "的" + _this.form.sex + "性" + _this.form.job + "工作者";

          _this.$confirm(status, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "success"
          }).then(function () {
            _this.dialogChoseStatus = console.log("this.form.marriage", _this.form.marriage);

            if (!_this.form.marriage) {
              _this.textShow = 1;
            } else if (_this.form.marriage === "已婚人士") {
              _this.textShow = 1;
            } else if (_this.form.marriage === "热恋中") {
              _this.textShow = 2;
            } else {
              _this.textShow = 3;
            }
          }).catch(function () {
            _this.resetForm("form");
          });
        } else {
          // console.log('error submit!!')
          return false;
        }
      });
    },
    resetForm: function resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};